.add-container {
  max-width: 500px;
  margin: 0 auto;

  padding: 20px;
  background-color: #f7f7f7;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}
h2 {
  color: rgb(40, 39, 39);
  text-align: center;
}
.input-group {
  margin-bottom: 15px;
  margin-right: 5%;
}

label {
  display: block;
  font-weight: bold;
  margin-bottom: 5px;
}

input {
  width: 70%;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

form .add-button {
  display: inline-block;
  background-color: #8731ce;
  color: white;
  border: none;
  padding: 12px 20px;
  border-radius: 5px;
  cursor: pointer;
  font-weight: bold;
  text-decoration: none;
  text-align: center;
  margin: 0 auto;
  margin-top: 10px;
  width: 73%;
  margin-left: 0%;
}
form .add-button:hover {
  background-color: #7f30c1;
  color: black;
}

form .back2-button {
  background-color: #8731ce;
  color: white;
  border: none;
  padding: 12px 20px;
  border-radius: 4px;
  cursor: pointer;
  width: 73.5%;
  margin-top: 10px;
  font-weight: bold;
  margin-left: 13%;
}
form{
  padding-right: 20px;
}
form input {
  margin-bottom: 10px;
  text-align: center;
  width: 70%;
  margin-left: 13.5%;
}


.back2-button:hover {
  background-color: #7f30c1;
}
@media (max-width: 768px) {
  .container {
    width: 80%;
    padding: 20px;
    padding-left: 0%;
    padding-right: 0%;
    margin: 0 auto;
  }
  form .back2-button {
    margin-left: 6%;
    width: 95%;
}
form .add-button {
  margin-top: 5%;
  margin-left: 6%; 
  width: 95%;   
} 
form input {
  margin-bottom: 10px;
  text-align: center;
  width: 90%;
  margin-left: 5%;
}

}